import "../styles/task.scss";
import { useState } from "react";
import React from 'react';

export default function Task(props) {
    const { addBug, deleteBug, moveBug, task } = props;

    const [urgencyLevel, setUrgencyLevel] = useState(task.urgency);
    const [collapsed, setCollapsed] = useState(task.isCollapsed);
    const [formAction, setFormAction] = useState("");

    function setUrgency(event) {
        setUrgencyLevel(event.target.attributes.urgency.value);
    }

    function handleSubmit(event) {
        event.preventDefault();

        if (formAction === "save") {
            if (collapsed) {
                setCollapsed(false);
            } else {
                let newTask = {
                    id: task.id,
                    title: event.target.elements.title.value,
                    description: event.target.elements.description.value,
                    urgency: urgencyLevel,
                    status: task.status,
                    isCollapsed: true
                };

                addBug(newTask);
                setCollapsed(true);
            }
        }

        if (formAction === "delete") {
            deleteBug(task.id);
        }
    }

    function handleMoveLeft() {
        let newStatus = "";

        if (task.status === "In Development") {
            newStatus = "Bug List";
        } else if (task.status === "QA") {
            newStatus = "In Development";
        }

        if (newStatus !== "") {
            moveBug(task.id, newStatus);
        }
    }

    function handleMoveRight() {
        let newStatus = "";

        if (task.status === "Bug List") {
            newStatus = "In Development";
        } else if (task.status === "In Development") {
            newStatus = "QA";
        }

        if (newStatus !== "") {
            moveBug(task.id, newStatus);
        }
    }

    return (
        <div className={`task ${collapsed ? "collapsedTask" : ""}`}>
            <button onClick={handleMoveLeft} className="button moveBug">
                &#171;
      </button>
            <form onSubmit={handleSubmit} className={collapsed ? "collapsed" : ""}>
                <input
                    type="text"
                    className="title input"
                    name="title"
                    placeholder="Enter Title"
                    disabled={collapsed}
                    defaultValue={task.title}
                />
                <textarea
                    rows="2"
                    className="description input"
                    name="description"
                    placeholder="Enter Description"
                    defaultValue={task.description}
                />
                <div className="urgencyLabels">
                    <label className={`low ${urgencyLevel === "low" ? "selected" : ""}`}>
                        <input
                            urgency="low"
                            onChange={setUrgency}
                            type="radio"
                            name="urgency"
                        />
            low
          </label>
                    <label
                        className={`medium ${urgencyLevel === "medium" ? "selected" : ""}`}
                    >
                        <input
                            urgency="medium"
                            onChange={setUrgency}
                            type="radio"
                            name="urgency"
                        />
            medium
          </label>
                    <label
                        className={`high ${urgencyLevel === "high" ? "selected" : ""}`}
                    >
                        <input
                            urgency="high"
                            onChange={setUrgency}
                            type="radio"
                            name="urgency"
                        />
            high
          </label>
                </div>
                <button
                    onClick={() => {
                        setFormAction("save");
                    }}
                    className="button"
                >
                    {collapsed ? "Edit" : "Save"}
                </button>
                {collapsed && (
                    <button
                        onClick={() => {
                            setFormAction("delete");
                        }}
                        className="button delete"
                    >
                        X
                    </button>
                )}
            </form>
            <button onClick={handleMoveRight} className="button moveBug">
                &#187;
      </button>
        </div>
    );
}
